.container {
   display: flex;
   justify-content: center;
   align-items: center; 
   width: 100%;
   height: 100vh;
}

@media screen and (max-width: 767px) {
   .container {
     background-image: url('../../images/mobile-background-element-sign-in.svg'),
       url('../../images/mobile-bottle-sign-in.svg');
     background-repeat: no-repeat, no-repeat;
     background-position: center, bottom;
   }
 }
 
 /* Tablet and > */
 @media screen and (min-width: 768px) {
   .container {
     background-image: url('../../images/mobile-background-element-sign-in.svg'),
       url('../../images/tablet-bottle-sign-in.svg');
     background-repeat: no-repeat, no-repeat;
     background-position: top, right;
   }
 }
 
 /* Desktop and > */
 @media screen and (min-width: 1440px) {
   .container {
     background-image: url('../../images/mobile-background-element-sign-in.svg'),
       url('../../images/desktop-bottle-sign-in.svg');
     background-repeat: repeat, no-repeat;
     background-position: center center, left;
   }
 }